import React, { useState, useEffect } from "react";
import { Mail, Loader, AlertCircle } from "lucide-react";
import "../../styles/auth.css";
import { UniversalLink as Link } from "../../database/UniversalLink";
import { useLanguage } from "../../i18n/LanguageContext";
export function Unregistered() {
    const { t } = useLanguage();
    const [resendLoading, setResendLoading] = useState(false);
    const [resendSuccess, setResendSuccess] = useState(false);
    const [error, setError] = useState("");
    const [cooldown, setCooldown] = useState(0);

    useEffect(() => {
        if (cooldown > 0) {
            const timer = setTimeout(() => setCooldown(c => c - 1), 1000);
            return () => clearTimeout(timer);
        }
    }, [cooldown]);

    const handleResendEmail = (e: React.FormEvent) => {
        e.preventDefault();
        if (cooldown > 0 || resendLoading) return;

        setResendLoading(true);
        setResendSuccess(false);
        setError("");

        // Simulate resend
        setTimeout(() => {
            // Simulate random failure (15% chance)
            if (Math.random() < 0.15) {
                setError(t('auth.unregistered.error'));
                setResendLoading(false);
                return;
            }

            setResendSuccess(true);
            setResendLoading(false);
            setCooldown(60);

            // Hide success message after 4 seconds
            setTimeout(() => {
                setResendSuccess(false);
            }, 4000);
        }, 1500);
    };

    return (
        <div className="auth-container">
            <div className="auth-bg">
                <div className="auth-gradient-orb orb-1"></div>
                <div className="auth-gradient-orb orb-2"></div>
                <div className="auth-gradient-orb orb-3"></div>
            </div>

            <Link to="/" className="auth-back-link">
                <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
                    <path
                        d="M15 10H5M5 10L10 15M5 10L10 5"
                        stroke="currentColor"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                    />
                </svg>
                {t('common.back')}
            </Link>

            <div className="auth-card">
                <div className="auth-header">
                    <div className="auth-icon-badge auth-error-badge">
                        <AlertCircle size={32} />
                    </div>
                    <h1 className="auth-title">{t('auth.unregistered.title')}</h1>
                    <p className="auth-subtitle">
                        {t('auth.unregistered.subtitle')}
                    </p>
                </div>

                <form onSubmit={handleResendEmail} className="auth-form">
                    {resendSuccess && (
                        <div className="auth-success-message">
                            {t('auth.unregistered.success')}
                        </div>
                    )}
                    {error && (
                        <div className="auth-error-message" style={{ color: '#ef4444', marginBottom: '1rem', textAlign: 'center' }}>
                            {error}
                        </div>
                    )}

                    <button type="submit" className="auth-submit-btn" disabled={resendLoading}>
                        {resendLoading ? (
                            <>
                                <span className="auth-spinner"></span>
                                {t('auth.unregistered.submitting')}
                            </>
                        ) : cooldown > 0 ? (
                            <>
                                <span style={{ opacity: 0.7 }}>
                                    {t('auth.unregistered.wait').replace('{seconds}', cooldown.toString())}
                                </span>
                            </>
                        ) : (
                            <>
                                <Mail size={18} />
                                {t('auth.unregistered.submit')}
                            </>
                        )}
                    </button>
                </form>

                <div className="auth-footer" style={{ marginTop: '20px' }}>
                    <p>
                        {t('auth.unregistered.footer')}
                        <Link to="/login" className="auth-link">
                            {t('common.login')}
                        </Link>
                    </p>
                </div>
            </div>
        </div >
    );
}
